  1. Use a more-intuitive box-sizing model.
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}
/*
    2. Remove default margin
  */
* {
  margin: 0;
}
/*
    Typographic tweaks!
    3. Add accessible line-height
    4. Improve text rendering
  */
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}
/*
    5. Improve media defaults
  */
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
/*
    6. Remove built-in form typography styles
  */
input,
button,
textarea,
select {
  font: inherit;
}
/*
    7. Avoid text overflows
  */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
/*
    8. Create a root stacking context
  */
#root,
#__next {
  isolation: isolate;
}

@font-face {
  font-family: 'SatoshiRegular';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('woff2');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'PoppinsSemiBold';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('woff2');
  font-weight: normal;
  font-style: normal;
}

:root {
  --heading-font: 'PoppinsSemiBold', sans-serif;
  --primary-font: 'SatoshiRegular', sans-serif;
}

/* General CSS */

body {
  background-color: #282828;
}

.hidden {
  display: none !important;
}

#gameArea {
  min-height: 100vh;
  width: 100vw;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}

#preGame {
  display: flex;
  flex-direction: column;
  gap: 50px;
  justify-content: center;
  align-items: center;
}

#preGame h1 {
  font-family: var(--heading-font);
  color: #f9f5d7;
}

#startGame {
  font-family: var(--primary-font);
  font-size: large;
  color: #282828;
  background-color: #98971a;
  border: none;
  width: 150px;
  height: 50px;
  border-radius: 2px;
}

#startGame:hover {
  cursor: pointer;
  background-color: #b8bb26;
}

#playArea {
  width: calc(90% + 40px);
  max-width: 1040px;
  margin: 0 auto;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

#boardLabel {
  font-family: var(--heading-font);
  color: #83a598;
  margin-bottom: 20px;
}

#gameboards {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
}

.board {
  width: calc(50% - 20px);
  max-width: 500px;
  aspect-ratio: 1 / 1;
  border: 2px solid #f9f5d7;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 1fr);
}

.tile {
}

.tile:hover {
  background-color: #333333;
  cursor: pointer;
}

#resetGame {
  color: #f9f5d7;
  background-color: #cc241d;
  font-family: var(--heading-font);
  border: none;
  width: 150px;
  height: 50px;
  border-radius: 2px;
  margin-top: 35px;
}

#resetGame:hover {
  background-color: #fb4934;
  cursor: pointer;
}

#playerBoard .ship {
  border: 1px solid #689d6a;
  background-color: #8ec07c;
}

#playerBoard .ship:hover {
  background-color: #a6d096;
}

#computerBoard .ship {
  /* background-color: gold; */
  background-color: #282828;
}

#computerBoard .ship:hover {
  background-color: #333333;
}

.hit {
  border: 1px solid #b16286;
  background-color: #e2a8b7;
}

.hit:hover {
  background-color: #f2c5d1;
}

#computerBoard .ship.hit,
#playerBoard .ship.hit {
  border: 1px solid #d79921;
  background-color: #f9cc64;
}

#computerBoard .ship.hit:hover,
#playerBoard .ship.hit:hover {
  background-color: #ffdb86;
}

#computerBoard .ship.hit.sunk,
#playerBoard .ship.hit.sunk {
  border: 1px solid #d65d0e;
  background-color: #fe8019;
}

#computerBoard .ship.hit.sunk:hover,
#playerBoard .ship.hit.sunk:hover {
  background-color: #ff9239;
}

@media (max-width: 768px) {
  #playArea {
    width: 90%;
    align-items: center;
  }

  #gameboards {
    flex-direction: column;
    align-items: center;
  }

  .board {
    width: 100%;
    max-width: 400px;
  }

  #gameboards > div:first-child {
    margin-bottom: 35px;
  }
}

#winScreen {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(5px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

#winScreen #winOptions {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#winScreen h1 {
  font-family: var(--heading-font);
  color: #f9f5d7;
  text-align: center;
}

#winScreen button {
  color: #f9f5d7;
  background-color: #cc241d;
  font-family: var(--heading-font);
  border: none;
  width: 150px;
  height: 50px;
  border-radius: 2px;
  margin-top: 20px;
  cursor: pointer;
}
